<template>
  <div class="megbox">
    <div class="top">
      <!-- 引用Emoji  里面有留言的输入框区域和表情区域  父传子  把rename传给输入框-->
      <Emoji ref="emoji" :textConent="rename" />

      <!-- prop属性？？？？？？？？？？ -->
      <el-form :inline="true" :model="ruleForm" class="demo-form-inline">
        <el-form-item prop="name" label="昵称" :rules="rules.name">
          <el-input v-model="ruleForm.name" placeholder="请输入昵称"></el-input>
        </el-form-item>
        <el-form-item prop="email" label="邮箱" :rules="rules.email">
          <el-input
            v-model="ruleForm.email"
            placeholder="请输入邮箱"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
    <h2>内容区</h2>
    <div class="bottom">
      <div class="block">
        <!-- list是Message.vue传过来的  type为1的留言 -->
        <div class="timeline" v-for="(v, index) in list" :key="index">
          <p>
            <!-- 留言的人的名称 -->
            <span>{{ v.megs.name }}</span
            >{{ v.megs.createdAt }}
          </p>
          <p class="content">{{ v.megs.content }}</p>

          <!-- 回复这里用id比较好吧   reindex回复的那一条的索引 -->
          <p class="reply" @click="replyMessage(index, v.megs.name)">回复</p>

          <!-- 回复的内容部分 -->
          <div class="rep" v-for="(r, i) in v.reply" :key="i">
            <svg class="icon inicon" aria-hidden="true">
              <use
                xlink:href="#icon-taidixiongwanoukeai"
                v-if="r.star == 0"
              ></use>
              <use xlink:href="#icon-labixiaoxin-" v-if="r.star == 1"></use>
            </svg>
            <p>
              <span>{{ r.name }}</span>
              {{ r.createdAt }}
            </p>
            <p class="content">{{ r.content }}</p>
            <p class="reply" @click="replyMessage(index, r.name)">回复</p>
          </div>

          <!-- 旁边的小图标 -->
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-labixiaoxin-" v-if="v.megs.star == 1"></use>
            <use
              xlink:href="#icon-taidixiongwanoukeai"
              v-if="v.megs.star == 0"
            ></use>
          </svg>
          <div class="line"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script> 
// 表情
import Emoji from "../components/Emoji";
import { addMessage, replyMessage } from "../api/index";
import { log } from 'three';

export default {
  data() {
    return {
      // ruleForm里面是装的输入的昵称和邮箱
      ruleForm: {},
      // 第一条留言 
      oneMeg: {
        type: 1,
        star: 0,
      },
      rules: {
        name: [
          { required: true, message: "请输入昵称", trigger: "blur" },
          {
            min: 1,
            max: 255,
            message: "长度在 1 到 255 个字符",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"],
          },
        ],
      },
      // 输入框中的内容保存在content中
      content: [],
      // 传给子组件Emoji.vue的信息 rename是要回复的那一条那个发言人的名字+@，rename会出现在留言框中
      rename: '',
      // 点击回复有关的， 回复的那一条留言的id
      uid: '',
      reindex: 0,
    };
  },
  // 父传子  这是子组件   父组件是Message.vue 和 Showart.vue
  props: {
    // Message.vue 传过来的,数据库中留言版的内容   里面有两个对象:megs和reply!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    list: {
      type: Array,
      // default: [],
      default: function () {
        return []
      }
    },
    // Message.vue 传过来的, 那边直接传了个 1（总留言板）  
    // 还有ShowArt传过来的,那边穿了个博客的id
    type: {
      default: "1",
    },
    // // 谁传给他的  他不应该在这里吧  把他移到data中   记录一下更改!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    // reindex: 0,
  },
  methods: {
    // ‘啾’提交留言按钮
    onSubmit() {
      // 保存输入的留言
      this.$refs.emoji.referContent();

      // 输入的留言保存在content中
      this.content = this.$refs.emoji.content;

      // 如果没有输入留言内容的话,直接return     
      if (this.content.length === 0) return

      // 输入留言的留言人名字、邮箱、内容、类型为1
      this.oneMeg.name = this.ruleForm.name;
      this.oneMeg.email = this.ruleForm.email;

      if (!this.ruleForm.name) return alert('请输入昵称')
      if (!this.ruleForm.email) return alert('请输入邮箱')

      // 将this.content中的内容存到this.oneMeg.content中
      this.oneMeg.content = this.content[this.content.length - 1];
      this.oneMeg.type = this.type;



      // 添加留言      this.rename == ''就是不是指回复别人的,而是自己添加留言
      if (this.rename == '') {
        addMessage(this.oneMeg)
          .then((res) => {
            if (res) {
              console.log(res);
              if (res.code == "200") {
                this.$message({
                  message: "发布成功",
                  type: "success",
                  duration: 1000,
                });
                // 将新项目添加到数组的开头
                this.list.unshift({ megs: this.oneMeg });

              } else {
                this.$message({
                  showClose: true,
                  message: "发布失败",
                  type: "error",
                  duration: 1000,
                });

              }
            }
          })
          .catch(function (error) {
            console.log(error);
            this.$message({
              showClose: true,
              message: "发布失败",
              type: "error",
              duration: 1000,
            });

          });
        this.clearInfo()
      }
      // rename不为空   就是指回复的是别人的 rename就是@+别人的名字
      else {
        // this.oneMeg.name 就是输入的昵称 + rename
        this.oneMeg.name += this.rename;
        // 回复的那一条留言的uid
        this.oneMeg.uid = this.uid;
        console.log("回复this.oneMeg:", this.oneMeg);
        replyMessage(this.oneMeg)
          .then((res) => {
            if (res) {
              console.log('回复别人的信息res', res);
              if (res.code == "200") {
                this.$message({
                  message: "发布成功",
                  type: "success",
                  duration: 1000,
                });
                console.log('megBox.vue中的this.list.reply回复留言', this.list.reply);
                // 把这条回复别人的回复添加到list里面的reply
                this.list[this.reindex].reply.push(this.oneMeg);
              } else {
                this.$message({
                  showClose: true,
                  message: "发布失败",
                  type: "error",
                  duration: 1000,
                });

              }
            }
          })
          .catch(function (error) {
            console.log(error);
            this.$message({
              showClose: true,
              message: "发布失败",
              type: "error",
              duration: 1000,
            });

          });
        this.clearInfo()
      }
    },

    // 点击回复， rename是要回复的那一条那个发言人的名字+@，rename会出现在留言框中
    replyMessage(i, name) {
      this.rename = '';

      console.log('回复的那一条的索引:', i);
      //   reindex 回复的那一条的索引
      this.reindex = i;

      let n = name.split("@")[0];
      console.log('n的值', n);

      // 回复：  zs@li的格式
      this.rename = `@${n}`;
      console.log('rename的值', this.rename);

      this.uid = this.list[i].megs.id;
      console.log('回复的那一条留言的uid', this.uid);
    },

    // 点完提交后,清空昵称,邮箱,rename
    clearInfo() {
      this.ruleForm.name = ''
      this.ruleForm.email = ''
      this.rename = ''
    }
  },
  components: {
    Emoji,
  },
};


</script>
