<template>
  <div class="mesg animate__animated animate__fadeInLeft">
    <img :src="megUrl" alt="" />
    <h2>欢迎大家留言啊 , 一起进步!(｡･∀･)ﾉﾞ</h2>
    <!-- 这两个属性是从子组件中拿过来的  把content和type传过去-->
    <MegBox :list="content" :type="1" />
  </div>
</template>

<script>
import { getMessage } from "../api/index";
import megUrl from "../assets/images/meg.gif";
import MegBox from "../components/MegBox";
export default {
  name: "Message",
  data() {
    return {
      // 请求数据得到的数据库中的留言板的内容
      content: [],
      megUrl: megUrl
    };
  },
  components: {
    MegBox,
  },
  created() {
    // 这里默认为1,因为要有外键!!!!!!!!!!!!!!!!!!!!!!!!让他对应第一篇水的博客
    getMessage(1)
      .then(res => {
        console.log('总的留言板,type设置为1：', res);
        this.content = res.data;
        console.log('Message.box中的content', this.content);
      })
      .catch(e => { console.log(e); })
  },
};
</script>
